* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  border: none; }

body {
  font-family: Arial, Helvetica, sans-serif; }

a {
  text-decoration: none; }

input:focus {
  outline: none; }

.container {
  max-width: 900px;
  margin: 0 auto;
  text-align: center; }
  .container .form-container {
    margin-top: 5rem;
    padding: 1rem;
    border: 2px solid #ccc;
    box-shadow: 2px 2px 2px #ccc; }
    .container .form-container .form-group {
      max-width: 600px;
      margin: 0px auto 15px; }
    .container .form-container .form-control {
      width: 100%;
      padding: 0.75rem;
      margin-top: 1rem;
      border: 1px solid #ccc;
      border-radius: 5px; }
  .container .header-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;
    background: #f9af08a6;
    padding: 0.5rem;
    margin-top: 4%;
    font-weight: 400;
    font-size: 17px; }
  .container table {
    width: 100%;
    margin: 1rem 0;
    border-collapse: collapse; }
    .container table td, .container table th {
      border: 1px solid #ddd;
      padding: 8px; }
    .container table tr:nth-child(even) {
      background: #ddd; }
    .container table th {
      background: #007bff;
      color: #fff; }
    .container table td a {
      color: #007bff; }
  .container .pagination {
    display: flex;
    align-self: left;
    align-items: flex-end;
    justify-content: flex-end; }
    .container .pagination p {
      padding: 0.5rem; }
      .container .pagination p .fas {
        color: #fbcb5e;
        cursor: pointer; }
  .container .userContainer {
    padding: 1rem;
    border: 1px solid #ccc; }
    .container .userContainer img {
      border-radius: 50%; }
    .container .userContainer p {
      padding: 0.5rem; }

.modal {
  position: fixed;
  top: 0;
  left: 0;
  display: none;
  width: 100%;
  height: 100%;
  z-index: 1050; }
  .modal .modal-dialog {
    position: relative;
    width: auto;
    margin: .5rem;
    pointer-events: none; }
  .modal .modal-content {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    pointer-events: auto;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: .3rem;
    outline: 0; }
  .modal .modal-header {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding: 1rem 1rem;
    border-bottom: 1px solid #dee2e6;
    border-top-left-radius: calc(.3rem - 1px);
    border-top-right-radius: calc(.3rem - 1px); }
  .modal .modal-body {
    position: relative;
    flex: 1 1 auto;
    padding: 1rem;
    text-align: left; }
  .modal .modal-footer {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: flex-end;
    padding: .75rem;
    border-top: 1px solid #dee2e6;
    border-bottom-right-radius: calc(.3rem - 1px);
    border-bottom-left-radius: calc(.3rem - 1px); }
    .modal .modal-footer .btn-secondary {
      margin-right: 0.5rem; }
  .modal h3 {
    font-weight: 300; }

.modal-backdrop, .loader-backdrop {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1040;
  width: 100vw;
  height: 100vh;
  background-color: #000;
  opacity: 0.5; }

.loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%; }
  .loader img {
    width: 50%; }

.btn {
  display: inline-block;
  padding: 0.375rem 1rem;
  cursor: pointer;
  border: 1px solid transparent;
  border-radius: 0.25rem;
  font-weight: 400;
  line-height: 1.5;
  font-size: 1rem;
  text-transform: uppercase; }
  .btn-primary {
    color: #fff;
    background-color: #007bff;
    border-color: #007bff; }
  .btn-link {
    color: #007bff;
    font-weight: bold;
    background: none; }
  .btn-danger {
    color: #fff;
    background-color: #dc3545;
    border-color: #dc3545;
    font-weight: bold; }
  .btn-secondary {
    color: #fff;
    background: #c3c0c0; }

.logout {
  font-size: 17px;
  margin-top: 5px;
  cursor: pointer; }

.alert {
  max-width: 600px;
  background-color: #f8d7da;
  border-color: #f5c6cb;
  color: #721c24;
  margin: auto;
  border-radius: 5px;
  padding: 10px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between; }

.close {
  padding: 1rem 1rem;
  margin: -1rem -1rem -1rem auto;
  float: right;
  font-size: 1.5rem;
  font-weight: 700;
  line-height: 1;
  color: #000;
  text-shadow: 0 1px 0 #fff;
  opacity: .5;
  background: none;
  cursor: pointer; }

@media (min-width: 576px) {
  .modal .modal-dialog {
    max-width: 500px;
    margin: 1.75rem auto; } }
